/* HAMBURGER MENU ICON */

/* Style navigation menu links */
.topnav a {
    display: block;

    padding: 15px 16px;

    color: white;
    font-size: 16px;
    text-decoration: none;
}

/* Style the hamburger menu */
.topnav a.icon {
    background: #FFAB0A;
    
    position: fixed;
    right: 0px;
