}

/* Add a background color on mouse-over */
.topnav a:hover {
    background-color: #2AC940;
    color: white;
    text-decoration: none;
}

/* ---------------------------------------- */
/* OVERLAY MENU */

/* The Overlay (background) */
.overlay {
    /* Width & height depends on how you want to reveal the overlay (see JS file) */
    width: 100%;
    height: 0%;
    
    /* background-color: #4569FF;
    opacity: 1; */
    background-color: rgb(39, 82, 255);     /* Color slightly darkened to compensate for opacity */
    opacity: 0.9;
    
    overflow-x: hidden; /* Disable horizontal scroll */
    
    position: fixed; /* Stay in place */
    top: 0;
    left: 0;
    
    z-index: 1; /* Sit on top */

    -webkit-transition: 0.5s;

    transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

/* Position the content inside the overlay */
.overlay-content {
    width: 100%; /* 100% width */
    margin-top: 30px; /* 30px top margin to avoid conflict with the close button on smaller screens */

    text-align: center; /* Centered text/links */

    position: relative;
    /* top: 19%; Percentage from the top  - Original position */
    top: 13%; /* Percentage from the top
    /* left: -1px;  offset a little here because the font is italic and there is an optical illusion
                    happening with the text seeming to appear slightly to the right */
    left: -2px; /*  offset a little here because the font is italic and there is an optical illusion
                    happening with the text seeming to appear slightly to the right */
}

/* -------------------- */
/* Position the close button (top right corner) */
.overlay .closebtn {
    color: white;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    font-style: normal;
    /* font-size: 100px;   Original size */
    font-size: 90px;
    text-decoration: none;
    
    cursor: pointer;
    
    position: absolute;
    /* top: 0px;       Original position */
    /* right: 45px;    Original position */
    top: -20px;
    right: 40px;

    -webkit-transition: 0.3s;

    transition: 0.3s; /* Transition effects on hover (color) */
}

.overlay .closebtn:hover {
    /* color: #FFAB0A; */
    color: rgb(238, 155, 0);    /* Slightly darker yellow to improve visibility */
    /* color: rgb(222, 144, 0); */
    /* font-size: 140px;   Original position */
    font-size: 130px;
    
    /* top: -26px;     Original position */
    /* right: 35px;    Original position */
    top: -46px;
    right: 30px;
}

/* -------------------- */
/* The navigation links inside the overlay */

/* Active Button */
/* Anchor (in React's case Link) for when the window URL DOES NOT match the Link className  */
a.menu-item {
    display: block; /* Display block instead of inline */

    color:white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    font-style: italic;
    /* font-size: 45px;    Original position */
    font-size: 32px;
    text-decoration: none;

    cursor: pointer;

    -webkit-transition: 0.3s;

    transition: 0.3s; /* Transition effects on hover (color) */
}

a.menu-item:hover, a.menu-item:focus {
    /* color: #FFAB0A; */
    color: rgb(238, 155, 0);    /* Slightly darker yellow to improve visibility */
    /* color: rgb(222, 144, 0); */
    /* font-size: 55px;    Original position */
    font-size: 42px;
    text-decoration: none;
}

/* -------------------- */
/* Inactive Button */
/* Anchor (in React's case Link) for when the window URL DOES match the Link className  */
a.menu-item--inactive {
    display: block; /* Display block instead of inline */

    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    font-style: italic;
    /* font-size: 45px;    Original position */
    font-size: 32px;
    text-decoration: none;

    cursor: pointer;
    
    -webkit-transition: 0.3s;
    
    transition: 0.3s; /* Transition effects on hover (color) */
}

a.menu-item--inactive:hover {
    /* color: #FFAB0A; */
    color: rgb(238, 155, 0);    /* Slightly darker yellow to improve visibility */
    /* color: rgb(222, 144, 0); */
    /* font-size: 55px;    Original position */
    font-size: 42px;
}

/* The following focus is optional depending on the effect you want after clicking the Link */
a.menu-item--inactive:focus {
    color: white;
    /* font-size: 45px;    Original position */
    font-size: 32px;
}

