    }

}

/* -------------------- */

/* When Screen Size Max-Width is 750px */
/* (375px is viewport width of iPhone X in vertical) */
@media screen and (max-width: 750px) {

    .overlay-content {
        /* top: 20%;   Original position */
        top: 10%;
    }

    .overlay .closebtn {
        position: absolute;
        /* top: 12px;  Original position */
        top: -10px;
        right: 30px;
        font-size: 70px;
    }

    .overlay .closebtn:hover {
        position: absolute;
        /* top: 12px;  Original position */
        top: -10px;
        right: 30px;
        font-size: 70px;
    }

    /* Active button */
    a.menu-item {
        font-size: 30px;
    }

    a.menu-item:hover, a.menu-item:focus {
        font-size: 30px;
    }

    /* Inactive button */
    a.menu-item--inactive {
        font-size: 30px;
    }

    a.menu-item--inactive:hover {
        font-size: 30px;
    }

}
